
@import url("//fonts.googleapis.com/css?family=Playfair+Display:900");
html {
  background-color: #fff;
  font-family: BlinkMacSystemFont, -apple-system, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", "Helvetica", "Arial", sans-serif;
  font-size: 1rem;
  -moz-osx-font-smoothing: grayscale;
  -webkit-font-smoothing: antialiased;
  min-width: 300px;
  overflow-x: hidden;
  overflow-y: scroll;
}

/*! minireset.css v0.0.2 | MIT License | github.com/jgthms/minireset.css */
html,
body,
p,
ol,
ul,
li,
dl,
dt,
dd,
blockquote,
figure,
fieldset,
legend,
textarea,
pre,
iframe,
hr,
h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  padding: 0;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: 100%;
  font-weight: normal;
}
ul {
  list-style: none;
}
button,
input,
select,
textarea {
  margin: 0;
}
html {
  box-sizing: border-box;
}
* {
  box-sizing: inherit;
}
*:before, *:after {
    box-sizing: inherit;
}
img,
embed,
object,
audio,
video {
  max-width: 100%;
}
iframe {
  border: 0;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}
td,
th {
  padding: 0;
  text-align: left;
}
body {
  color: black;
  font-size: 1rem;
  font-weight: normal;
  line-height: 1.5;
  overflow-x: hidden;
}
#app {
  background-color: white;
  margin: 0 auto;
  height: 100vh;
  width: 100vw;
  min-width: 20em;
  overflow-x: hidden;
  position: relative;
}
.header {
  background: white;
  box-shadow: 0 0 6px 4px rgba(0, 0, 0, 0.08);
  height: 80px;
  position: absolute;
  top: 0;
  left: 0;
  opacity: 0;
  -webkit-transform: translateY(-80px);
          transform: translateY(-80px);
  transition: all cubic-bezier(0.25, 0.1, 0.25, 1) 200ms;
  width: 100vw;
  will-change: opacity, transform;
  z-index: 30;
}
.open .header {
  -webkit-transform: translateY(0);
          transform: translateY(0);
  opacity: 1;
}
.logo {
  background-image: url("./img/logo-2x.png");
  background-size: 265px 29px;
  position: absolute;
  z-index: 99;
  width: 265px;
  height: 29px;
  top: 32px;
  left: 0;
  right: 0;
  margin: 0 auto;
}
.menu-link {
  padding: 0.2em;
}
.menu-label {
  color: white;
  text-transform: none;
  position: absolute;
  font-size: 14px;
  top: 37px;
  left: 6vw;
  transition: color 100ms cubic-bezier(0.25, 0.1, 0.25, 1);
  z-index: 9999;
}
.menu-label:hover {
    cursor: pointer;
}
.menu-label b {
    transition: color 100ms cubic-bezier(0.25, 0.1, 0.25, 1);
}
.icon-menu-open {
  stroke: white;
  stroke-width: 1.5;
}
.icon-menu-1,
.icon-menu-2,
.icon-menu-3 {
  transition: all 250ms cubic-bezier(0.25, 0.1, 0.25, 1);
}
.open {
  color: black;
}
.open .icon-menu-open {
    stroke: black;
}
.open .icon-menu-1 {
    -webkit-transform-origin: top right;
            transform-origin: top right;
    -webkit-transform: rotate(-45deg);
            transform: rotate(-45deg);
}
.open .icon-menu-2 {
    opacity: 0;
}
.open .icon-menu-3 {
    -webkit-transform-origin: bottom right;
            transform-origin: bottom right;
    -webkit-transform: rotate(45deg);
            transform: rotate(45deg);
}
button {
  background: none;
  border: none;
  cursor: pointer;
}
button:focus {
    outline: none;
}
.content,
.nav-content {
  background-color: white;
  height: 100vh;
  width: 100%;
  padding: 2em 0 0;
  position: absolute;
  top: 0;
  left: 0;
}
.nav-content {
  width: 0;
  z-index: 5;
}
.content {
  background-position: top center;
  background-repeat: no-repeat;
  background-size: cover;
}
.tagline {
  position: absolute;
  top: 30vh;
  left: 6vw;
  width: 80vw;
}
@media (min-width: 675px) {
.tagline {
      width: 50vw;
}
}
.tagline h1 {
    color: white;
    font-family: 'Playfair Display', serif;
    font-weight: 900;
    font-size: 2em;
    margin: 0 0 1em;
}
.button-donate {
  background-color: #FFCD00;
  letter-spacing: 1.29px;
  padding: 1em 1.5em;
  transition: -webkit-transform cubic-bezier(0.25, 0.1, 0.25, 1) 300ms;
  transition: transform cubic-bezier(0.25, 0.1, 0.25, 1) 300ms;
  transition: transform cubic-bezier(0.25, 0.1, 0.25, 1) 300ms, -webkit-transform cubic-bezier(0.25, 0.1, 0.25, 1) 300ms;
}
.button-donate:hover {
    -webkit-transform: scale(1.08);
            transform: scale(1.08);
}
a {
  color: black;
  cursor: pointer;
  text-decoration: none;
}
a:hover {
    color: black;
}
a:visited {
    color: black;
}
.is-hidden {
  display: none !important;
}
.show-accessible {
  position: absolute !important;
  top: -9999px !important;
  left: -9999px !important;
}
.side-content-wrapper {
  -webkit-box-flex: 1;
      -ms-flex: 1 1 auto;
          flex: 1 1 auto;
  height: 100%;
  position: relative;
  will-change: opacity;
  z-index: 9;
}
@media (min-width: 675px) {
.side-content-wrapper {
      -webkit-box-flex: 1;
          -ms-flex: 1 1 0px;
              flex: 1 1 0;
      overflow-y: scroll;
}
}
.side-content {
  padding: 2em;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  position: absolute;
  z-index: 30;
}
.card {
  -webkit-box-flex: 1;
      -ms-flex: 1 1 0px;
          flex: 1 1 0;
  display: inline-block;
  margin: 0 2em 2em 0;
  max-width: 300px;
  min-width: 150px;
  opacity: 0;
}
.card-subtitle {
  color: #878080;
  display: block;
  font-weight: 500;
  margin-top: 0.75em;
}
.title {
  font-weight: 500;
  padding: 0.5em 0 1em;
}
.info {
  color: #878080;
  font-size: 0.875em;
}
.menus-container,
.menus {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  position: relative;
  z-index: 20;
}
.menus-container {
  height: calc(100% - (80px));
  margin-top: 80px;
}
.menus {
  margin-top: 80px;
}
.menu-primary {
  display: inline-block;
  -webkit-box-flex: 0;
      -ms-flex: 0 1 auto;
          flex: 0 1 auto;
  line-height: 2;
  margin: 0 4em 0 0;
  position: relative;
  z-index: 6;
}
.menu-primary li {
    opacity: 0;
    will-change: transform, opacity;
}
.menu-primary a {
    display: inline-block;
    transition: all 0.45s cubic-bezier(0.25, 0.1, 0.25, 1);
    position: relative;
}
.menu-primary a:before {
      content: '';
      background: #FFCD00;
      display: inline-block;
      position: absolute;
      left: -18px;
      top: 15px;
      width: 0;
      height: 3px;
      opacity: 0;
      transition: all 300ms cubic-bezier(0.25, 0.1, 0.25, 1);
}
.menu-primary a:hover,
  .menu-primary a.active {
    font-weight: 600;
    -webkit-transform: translateX(20px) scale(1.08);
            transform: translateX(20px) scale(1.08);
}
.menu-primary a:hover:before,
    .menu-primary a.active:before {
      opacity: 1;
      width: 10px;
}
.menu-secondary {
  display: inline-block;
  -webkit-box-flex: 1;
      -ms-flex: 1 1 0px;
          flex: 1 1 0;
  line-height: 2.4;
  margin-top: -3px;
  margin-right: 5em;
  position: relative;
  z-index: 6;
}
.menu-secondary li {
    opacity: 0;
    will-change: transform, opacity;
}
.menu-secondary li a {
      color: #878080;
      display: inline-block;
      font-size: 0.75em;
      text-transform: uppercase;
      transition: all 0.35s cubic-bezier(0.25, 0.1, 0.25, 1);
}
.menu-secondary li a:hover,
    .menu-secondary li a.active {
      color: black;
      -webkit-transform: translateX(5px);
              transform: translateX(5px);
}



